.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.rocksGlass {
  -webkit-animation: shake 1s infinite ease;
          animation: shake 1s infinite ease;
  max-height: 80vh;
}

.meals > .logo {
  -webkit-animation: shake 1.5s infinite ease;
          animation: shake 1.5s infinite ease;
  color: aliceblue;
  font-family: Montserrat , sans-serif;
  font-size: 3em;
  font-weight: 900;
}

@-webkit-keyframes shake {

  0% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }

  50% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }

  100% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
}

@keyframes shake {

  0% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }

  50% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }

  100% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
}

@-webkit-keyframes clink {

  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }

  50% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

@keyframes clink {

  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }

  50% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }

  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

:root {

  --pading-base: 1em;
}

.form-horizontal {
