.enter-btn {
  -webkit-animation: btn 25s infinite;
          animation: btn 25s infinite;
  border: none;
  border-radius: 6px;
  color: white;
  margin-top: 1em;
  outline-style: none;
  padding: 1em;
  padding: var(--pading-base);
